import React from 'react'
import { Link } from 'react-router-dom'
import { InfoBox } from '../components/InfoBox'
import { CartItem } from '../components/CartItem'
import CartPageSvg from '../svg/CartPageSvg'
import BasketSvg from '../svg/BasketSvg'
import BackArrowSvg from '../svg/BackArrowSvg'
import { useSelector, useDispatch } from 'react-redux'
import { clearItems, selectCart } from '../redux/slices/cartSlice'

export const Cart: React.FC = () => {
  const dispatch = useDispatch()
  const { totalCount, totalPrice, items } = useSelector(selectCart)
  const onClickClear = () => {
    if (window.confirm(' ?')) {
      dispatch(clearItems())
    }
  }

  const onClickPay = () => {
    if (window.confirm(`  ${totalCount}    ${totalPrice} `)) {
      dispatch(clearItems())
    }
  }

  return (
    <div className='content'>
      {items.length > 0 ? (
        <div className='container container--cart'>
          <div className='cart'>
            <div className='cart__top'>
              <h2 className='content__title'><CartPageSvg /></h2>
              <div onClick={onClickClear} className='cart__clear'><BasketSvg /><span> </span></div>
            </div>
            <div className='content__items'>
              {items.map((item: any) =>
                <CartItem key={item.id} {...item} />)
              }
            </div>
            <div className='cart__bottom'>
              <div className='cart__bottom-details'>
                <span>  : <b>{totalCount} .</b> </span>
                <span>  : <b>{totalPrice} </b> </span>
              </div>
              <div className='cart__bottom-buttons'>
                <Link to={`/`} className='button button--outline button--add go-back-btn'>
                  <BackArrowSvg /><span> </span>
                </Link>
                <button onClick={onClickPay} className='button pay-btn'>
                  <span> </span>
                </button>
              </div>
            </div>
          </div>
        </div>
      )
        :
        <InfoBox
          title=' '
          description=' ,     .   ,   ,    .'
          buttonTitle=' '
          alt=' '
        />
      }
    </div>
  )
}